export const maxNumberGameInit = () => {
  const gameContainer = document.querySelector('.biggest-number');

  const gameHtml = `
    <div class="max-number-game__container">
      <h2 class="game__title">–í–≤–µ–¥—ñ—Ç—å 3 —á–∏—Å–ª–∞</h2>
      <div class="max-number-game__wrapper">
        <form class="max-number-game__form" data-max_number_form>
          <input class="max-number-game__input" type="number" name="number1" placeholder="–í–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ" required />
          <input class="max-number-game__input" type="number" name="number2" placeholder="–í–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ" required />
          <input class="max-number-game__input" type="number" name="number3" placeholder="–í–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ" required />
        </form>
        <div class="max-number-game__result">
          <p data-max_number_output>–ù–∞–π–±—ñ–ª—å—à–µ —á–∏—Å–ª–æ, —è–∫–µ –≤–∏ –≤–≤–µ–ª–∏ - (—á–∏—Å–ª–æ)</p>
        </div>
      </div>
    </div>
  `;

  if (gameContainer) {
    gameContainer.innerHTML = gameHtml;

    const form = document.querySelector('[data-max_number_form]');
    const outputElement = document.querySelector('[data-max_number_output]');

    const handleInput = () => {
      const num1 = parseFloat(form.number1.value);
      const num2 = parseFloat(form.number2.value);
      const num3 = parseFloat(form.number3.value);

      if ([num1, num2, num3].some(num => isNaN(num))) {
        outputElement.textContent = '–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å —É—Å—ñ —Ç—Ä–∏ —á–∏—Å–ª–∞.';
        return;
      }

      const max = Math.max(num1, num2, num3);
      outputElement.textContent = `–ù–∞–π–±—ñ–ª—å—à–µ —á–∏—Å–ª–æ, —è–∫–µ –≤–∏ –≤–≤–µ–ª–∏ - ${max}`;
    };

    form.addEventListener('input', handleInput);
  }
};
